<template>
  <div class="detail">
    <detail-top-bar @stop="stop"/>
    <div class="content" v-if="currentMusicDetail !== null">
      <div class="detail-img">
        <img :src="currentMusicDetail.songPic" alt />
      </div>
      <div class="glorious">
        <div class="glorious1"></div>
        <div class="glorious2"></div>
      </div>
      <div class="detail-song-info">
        <span>{{currentMusicDetail.songName}}</span>--
        <span>{{currentMusicDetail.artistName}}</span>
      </div>
      <div class="time">
        <span class="play-time">{{playTime | formatTime}}</span>
        <span></span>
        <span class="all-time">{{currentMusicDetail.time | formatTime}}</span>
      </div>
      <!-- <audio
        src
        ref="audio"
        crossorigin="anonymous"
        @canplay="getDuration"
        @timeupdate="getCurrentTime"
        loop
      ></audio>-->
      <canvas id="canvas" class="canvas">Your browser does not support Canvas tag.</canvas>
    </div>
    <detail-player />
  </div>
</template>

<script>
import DetailTopBar from "./childComp/DetailTopBar";
import DetailPlayer from "./childComp/DetailPlayer";
import { getMusicDetail } from "network/song";
export default {
  data() {
    return {
      id: this.$store.state.currentMusic.song_id,
      currentMusicDetail: {},
      isPlay: true,
      currentTime: 0,
      audioContext: null,
      analyser: null,
      source: null,
      offset: 0, //
      startTime: null, // 
      playTime: 0
    };
  },
  components: {
    DetailTopBar,
    DetailPlayer,
  },
  created() {
    this.getMusicDetail(this.id);
    //
    this.$bus.$on("getCurrentMusicId", () => {
      this.getCurrentMusicId();
      this.getMusicDetail(this.id);
    });
    this.$bus.$on("playControl", (isPlay) => {
      if (!isPlay) {
        this.pause();
      } else {
        this.play();
      }
    });
  },
  mounted() {
    this.createAudioContext();
    //
    const getPlayTime = () => {
      window.requestAnimationFrame(getPlayTime);
      this.playTime = this.getPosition();
    }
    getPlayTime();
  },
  methods: {
    getCurrentMusicId() {
      this.id = this.$store.state.currentMusic.song_id;
    },
    getMusicDetail(id) {
      getMusicDetail(id).then((res) => {
        this.currentMusicDetail = res.data;
        this.getAudio();
      });
    },
    //web audio api
    createAudioContext() {
      //
      const AudioContext = window.AudioContext || window.webkitAudioContext;
      this.audioContext = new AudioContext(); //
      //analyserNode
      this.analyser = this.audioContext.createAnalyser();
      this.analyser.fftSize = 256; //
    },
    //
    getAudio() {
      this.stop(); //
      this.play(); //
      this.getCanvas(); //
    },
    //
    play() {
      if (!this.currentMusicDetail || this.source) {
        return; //
      }
      const xhr = new XMLHttpRequest();
      xhr.open("GET", this.currentMusicDetail.songLink, true);
      //  arraybuffer
      xhr.responseType = "arraybuffer";
      const that = this;
      xhr.onload = function () {
        that.source = that.audioContext.createBufferSource();
        // 
        that.audioContext.decodeAudioData(xhr.response, function (buffer) {
          // buffer
          that.source.buffer = buffer;
          // ====
          that.source.connect(that.analyser);
          that.analyser.connect(that.audioContext.destination);
          //
          that.startTime = that.audioContext.currentTime;
          that.source.start(0, that.offset);
        });
      };
      xhr.send();
    },
    //
    pause() {
      if (!this.currentMusicDetail || !this.source) return;
      this.source.stop(0);
      this.source.disconnect(0);
      this.source = null;
      this.offset = this.getPosition();
      this.startTime = null;
    },
    //
    stop() {
      this.pause();
      this.offset = 0;
    },
    //
    getPosition() {
      return (
        this.offset +
        (this.startTime !== null
          ? this.audioContext.currentTime - this.startTime
          : 0)
      );
    },
    //
    getCanvas() {
      //canvas
      const canvas = document.getElementById("canvas");
      const cxt = canvas.getContext("2d");
      const WIDTH = canvas.width;
      const HEIGHT = canvas.height;
      const lineWidth = 4;
      const gap = 6;
      const maxNum = Math.round(WIDTH / (lineWidth + gap));
      cxt.strokeStyle = "red";
      cxt.lineCap = "round";
      cxt.lineWidth = lineWidth;
      const that = this;
      function render() {
        cxt.clearRect(0, 0, WIDTH, HEIGHT); //
        //that.analyser.frequencyBinCountfftSize
        const dataArray = new Uint8Array(that.analyser.frequencyBinCount);
        that.analyser.getByteFrequencyData(dataArray); //
        for (let i = 0; i < maxNum; i++) {
          cxt.strokeStyle = "#008b8b";
          const value = Math.round((dataArray[i] / 128) * 20);
          cxt.beginPath();
          cxt.moveTo(i * (lineWidth + gap) + gap / 2, HEIGHT / 2);
          cxt.lineTo(i * (lineWidth + gap) + gap / 2, HEIGHT / 2 - value - 2);
          cxt.stroke();
          cxt.beginPath();
          cxt.moveTo(i * (lineWidth + gap) + gap / 2, HEIGHT / 2);
          cxt.lineTo(i * (lineWidth + gap) + gap / 2, HEIGHT / 2 + value + 2);
          cxt.stroke();
        }
        window.requestAnimationFrame(render); //
      }
      // requestAnimationFrame()
      window.requestAnimationFrame(render);
    },
  },
  computed: {
    
  },
  filters: {
    formatTime(time) {
      let minute = parseInt(time / 60);
      let second = parseInt(time % 60);
      if (second < 10) {
        second = "0" + second;
      }
      let ftime = minute + ":" + second;
      return ftime;
    },
  },
};
</script>

<style scoped>
.content {
  height: calc(100vh - 200px);
}
.detail {
  position: relative;
  width: 100vw;
  height: 100vh;
}
.detail-img {
  position: absolute;
  left: 50%;
  top: 20%;
  transform: translateX(-50%);
  width: 200px;
  height: 200px;
  border-radius: 50%;
  overflow: hidden;
  background: #008b8b;
  z-index: 1;
}

.detail-img img {
  width: 100%;
  height: 100%;
}
.glorious {
  position: absolute;
  left: 50%;
  top: 17%;
  transform: translateX(-50%);
  width: 300px;
  height: 250px;
}
.glorious1 {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 300px;
  height: 100px;
  border-radius: 80%;
  background: hsla(184, 61%, 81%, 0.678);
  animation: narrow 2s infinite;
  box-shadow: 0 0 12px rgba(211, 219, 219, 0.6);
  z-index: 1;
}
.glorious2 {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 180px;
  height: 250px;
  border-radius: 80%;
  background: rgba(188, 200, 201, 0.678);
  box-shadow: 0 0 12px rgba(175, 184, 184, 0.6);
  animation: widthen 2s infinite;
}
@keyframes narrow {
  0% {
  }
  50% {
    width: 250px;
    height: 140px;
  }
  100% {
    width: 300px;
    height: 100px;
  }
}
@keyframes widthen {
  0% {
  }
  50% {
    width: 250px;
    height: 160px;
  }
  100% {
    width: 180px;
    height: 250px;
  }
}
.detail-song-info {
  position: absolute;
  top: 58%;
  left: 50%;
  transform: translateX(-50%);
  white-space: nowrap;
}
.time {
  position: absolute;
  display: flex;
  justify-content: space-between;
  left: 50%;
  top: 80%;
  transform: translateX(-50%);
  width: 100px;
  font-weight: 600;
  color: #333;
}
.time span:nth-child(2) {
  position: relative;
  top: -10px;
  border-width: 20px 3px;
  border-style: solid;
  border-color: transparent transparent #93d5dc transparent;
}
.canvas {
  position: absolute;
  top: 63%;
  left: 50%;
  transform: translateX(-50%);
  width: 300px;
  height: 100px;
}
</style>